export default {
	data() {
		return {
			version: '1.0.0'
		}
	},
	onLoad: function() {



	},
	methods: {
		tapVersion: function(e) {
			uni.showModal({
				title: '是否注销账号？',
				content: '账号注销后，账号资料不可恢复！',
				showCancel: true,
				cancelColor: '#929292',
				confirmColor: '#3860ff',
				success: (res) => {
					if (res.confirm) {
						console.log('用户点击确认');
						uni.request({
							url: this.$Url + '/api/v1/cancellation',
							method: 'POST',
							data: {
								token: uni.getStorageSync('token')
							},
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							success: (res) => {
								if (res.data.code == 200) {
									console.log('注销成功')
								}
							}
						});
						uni.setStorageSync('userData', '');
						uni.setStorageSync('logon_status', 0);
						uni.setStorageSync('token', 0);
						uni.redirectTo({
							url: '../../login/login?form=1'
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	}
}
